
import { store } from '@/store';
import subpage from '@/components/subpage.vue';
import { ref } from 'vue';

export default {
  components: { subpage },
  setup() {
    type galleryType = 'all' | 'ads' | 'faces' | 'dance' | 'nudes' | 'fashion';
    let gallery: {
      ads: string[];
      faces: string[];
      dance: string[];
      nudes: string[];
      fashion: string[];
    } = {
      ads: require.context('../../public/images/portfolio/advertisement', true).keys(),
      faces: require.context('../../public/images/portfolio/faces', true).keys(),
      dance: require.context('../../public/images/portfolio/dance', true).keys(),
      nudes: require.context('../../public/images/portfolio/nudes', true).keys(),
      fashion: require.context('../../public/images/portfolio/fashion', true).keys(),
    };

    function convertToRelativePath(folderName: string, value: string[]): string[] {
      const newVal: string[] = [];
      for (const iterator of value) {
        newVal.push(
          require(`../../public/images/portfolio/${folderName}${iterator.replace('.', '')}`)
        );
      }
      return newVal;
    }

    gallery.ads = convertToRelativePath('advertisement', gallery.ads);
    gallery.faces = convertToRelativePath('faces', gallery.faces);
    gallery.dance = convertToRelativePath('dance', gallery.dance);
    gallery.nudes = convertToRelativePath('nudes', gallery.nudes);
    gallery.fashion = convertToRelativePath('fashion', gallery.fashion);

    // const allGallery = [...gallery.ads, ...gallery.faces];

    const currentGalleryName = ref<galleryType>('all');
    const currentGalleryImages = ref<{ type: galleryType; image }[]>([]);

    addFromGallery('ads', currentGalleryImages.value);
    addFromGallery('faces', currentGalleryImages.value);
    addFromGallery('dance', currentGalleryImages.value);
    addFromGallery('nudes', currentGalleryImages.value);
    addFromGallery('fashion', currentGalleryImages.value);

    function setGallery(value: galleryType) {
      currentGalleryName.value = value;
      removeWhereNot(value, currentGalleryImages.value);
      if (value == 'all') {
        addFromGallery('ads', currentGalleryImages.value);
        addFromGallery('faces', currentGalleryImages.value);
        addFromGallery('dance', currentGalleryImages.value);
        addFromGallery('nudes', currentGalleryImages.value);
        addFromGallery('fashion', currentGalleryImages.value);
      }
      if (value == 'ads') {
        addFromGallery('ads', currentGalleryImages.value);
      }
      if (value == 'faces') {
        addFromGallery('faces', currentGalleryImages.value);
      }
      if (value == 'dance') {
        addFromGallery('dance', currentGalleryImages.value);
      }
      if (value == 'nudes') {
        addFromGallery('faces', currentGalleryImages.value);
      }
      if (value == 'fashion') {
        addFromGallery('fashion', currentGalleryImages.value);
      }
    }

    function addFromGallery(value: galleryType, current: { type: galleryType; image }[]): void {
      for (const iterator of gallery[value]) {
        if (
          !current.some((element) => {
            if (element.image === iterator) {
              return true;
            }

            return false;
          })
        ) {
          current.push({ type: value, image: iterator });
        }
      }
    }

    function removeWhereNot(value: galleryType, current: { type: galleryType; image }[]): void {
      const indexToRemove: number[] = [];
      for (let index = current.length - 1; index >= 0; index--) {
        const element = current[index];
        if (element.type !== value) {
          indexToRemove.push(index);
        }
      }
      for (let index = 0; index < indexToRemove.length; index++) {
        const element = indexToRemove[index];
        current.splice(element, 1);
      }
    }

    function onBeforeLeaveTransition(element: Element) {
      const element2 = element as HTMLElement;
      //   console.log(element2);
      element2.style.height = element.clientHeight + 'px';
      element2.style.width = element.clientWidth + 'px';
      //   console.log(element.getBoundingClientRect());
      //   console.log(element.getBoundingClientRect());

      //   element2.style.top = '';
      //   element2.style.left = '';
    }

    function onLeaveTransition(element: Element) {
      const element2 = element as HTMLElement;
      //   console.log(element2);
      //   console.log(element.getBoundingClientRect());

      //   element2.style.top = element.getBoundingClientRect().top + 'px';
      //   element2.style.left = element.getBoundingClientRect().left + 'px';
    }

    function onAfterEnter(element: Element) {
      //   const element2 = element as HTMLElement;
      //   console.log(element.getBoundingClientRect());
      //   element2.style.top = element.getBoundingClientRect().top + 'px';
      //   element2.style.left = element.getBoundingClientRect().left + 'px';
    }

    return {
      onAfterEnter,
      onBeforeLeaveTransition,
      onLeaveTransition,
      store,
      gallery: {
        currentGalleryName,
        currentGalleryImages,
      },
      functions: {
        setGallery,
      },
    };
  },
};
