  //About page
  aboutHeader: { de: 'about', en: 'null' },
  aboutHeaderText: {
    de: `Sascha Hüttenhain wird im südlichen Nordrhein-Westfalen geboren.
    Dort aufgewachsen, entdeckt er die Leidenschaft zur Fotografie und beginnt, seine Ideen und Inspirationen in künstlerischen Bildern auszudrücken.
    Anfangs arbeitet er für verschiedene Fotografen und begleitet sie bei deren täglicher Arbeit, bis ihn sein Weg in die Selbständigkeit führt.
    Seitdem ist er als freischaffender Fotograf für nationale und internationale Kunden aus den unterschiedlichsten Branchen tätig und setzt Arbeiten in seinen Fotostudios in Frankfurt oder Siegen um.
    Die Mischung aus technischem Verständnis und Kreativität entspricht seinen persönlichen Fähigkeiten und Interessen.
    Das Spiel mit Licht und Schatten, für das er besonders gern Fotostudios nutzt, reizt ihn besonders und macht seine Fotografie und Bildsprache aus.
    Sein Stil wird oftmals als reduziert und minimalistisch und somit aufs Wesentliche beschränkt, bezeichnet.
    Der Fokus seiner Arbeit liegt vor allem in den Bereichen Portrait, Werbung, Sport und Mode, welche er on Location oder im Studio umsetzt.`,
    en: 'null',
  },
