  //prints page
  printsHeader: {
    de: 'EXKLUSIVE FINE ART PRINTS',
    en: 'null',
  },
  printsHeaderText: {
    de: `Eine Fotografie wird erst mit dem Druck auf Papier geboren und zum Leben erweckt.
    Jedes Werk, das in digitaler Form gezeigt wird, ist lediglich eine Projektion der Vision des Künstlers.
    Vor der eigentlichen Aufnahme beginnt alles mit der Ideenfindung und dem Eintauchen in das Thema, sowie der Entschlossenheit, ein Bild zu machen und setzt sich mit der Nachbearbeitung fort.
    Der letzte Teil zur Erstellung eines Fotowerks ist der Druckprozess, welcher ein eigenes und besonderes Gebiet darstellt.
    Viele technische Details müssen geprüft, Spezifikationen eingehalten und hochwertige Tinten, sowie das passende Papier ausgewählt werden.
    Wer einen Qualitätsprint erwirbt weiß, daß er nicht nur Tinte und Papier kauft, sondern ein Kunstwerk, das die Seele des Künstlers widerspiegelt.
    Fotodrucke werden als wertvolle Kunstwerke wahrgenommen, ähnlich wie eine Skulptur oder ein Gemälde.`,
    en: 'null',
  },
  printsContentText2: {
    de: `Die Qualität, mit der wir unsere Drucke ausliefern, ist hinsichtlich Material und Auflösung auf höchstem Niveau, so dass sie für die Dekoration von Wohnräumen, Interieurs, Büros oder öffentlichen Bereichen ideal geeignet sind.
    
    Um diesen Anspruch weiterzuführen, haben wir uns entschieden, nur mit Premiumpartnern zu arbeiten.
    Dazu verwenden wir Papier von Hahnemühle und Bilderrahmen von Halbe.`,
    en: 'null',
  },
